.screen-left .ant-table-thead > tr > th,
.screen-left .ant-table-tbody > tr > td {
  text-align: center !important;
}
.screen-left .ant-table-row:nth-child(even):hover {
  background: #F7F9FB !important;
}
.screen-left .ant-table-row:nth-child(even):hover td {
  background: none !important;
}
.screen-left .ant-table-row:nth-child(odd):hover {
  background: #fff !important;
}
.screen-left .ant-table-row:nth-child(odd):hover td {
  background: none !important;
}
.screen-left .ant-tabs-tab-active {
  color: #333;
}
.screen-left .ant-tabs-tab {
  color: #2A283D;
}
.screen-left .ant-tabs-tab:hover {
  color: #333;
}
.screen-left .ant-table-body {
  border: 1px solid #F3F3F3;
}
.screen-left .statistics-pagination {
  text-align: right;
  margin: 15px 0;
}
.statistics_tab {
  height: 51px;
  margin-bottom: 20px;
}
.statistics_tab div {
  height: 100%;
}
.statistics_tab .ant-tabs-tab {
  color: #2A283D;
  padding: 0;
  line-height: 51px;
  font-weight: 500;
  font-family: Alibaba PuHuiTi;
}
.statistics_tab .ant-tabs-tab-active {
  color: #333333;
  font-weight: 700;
  height: 51px;
  border-bottom: solid 2px #3A89FD;
}
.statistics_tab .ant-tabs-nav-scroll {
  border-bottom: 1px solid #EEEEEE;
  margin-bottom: 20px;
}
.screen-left .screen-left-content > div {
  padding: 0px 30px;
  padding-bottom: 20px;
}
.statistics .ant-table-thead > tr > th,
.statistics .ant-table-tbody > tr > td {
  padding: 0;
  height: 49px;
  color: #333333;
}
.statistics .statistics_table .ant-table-thead > tr > th {
  border-bottom: none;
}
.statistics .statistics_table .ant-table-body {
  border: none;
}
.statistics .statistics_table .ant-table-tbody > tr > td {
  border-bottom: 1px solid #F3F3F3;
}
.statistics .statistics_table th {
  background: #F7F9FB !important;
  height: 40px !important;
  padding: 0;
}
.statistics .statistics_table th .ant-table-column-title {
  color: #000000;
  font-size: 14px;
}
.statistics .statistics_table tr:nth-child(even) {
  background: #F7F9FB;
}
.statistics .statistics_table .ant-table-placeholder {
  border: none;
}
.staristics_border {
  border: 1px solid #F3F3F3;
  height: calc(100% - 77px);
  position: relative;
}
.staristics_border .statistics-pagination {
  position: absolute;
  bottom: 0px;
  right: 8px;
  margin-right: 25px;
}
.staristics_border .statistics-pagination .ant-pagination-options {
  float: right;
}
.staristics_border .statistics-pagination .ant-pagination-total-text {
  display: none;
}
.screen-left-content .ant-empty .ant-empty-image {
  width: auto;
  height: auto;
  margin-bottom: 0px;
}
.statistics .screen-left .screen-left-content {
  height: calc(100% - 100px);
  padding-bottom: 20px;
  overflow: hidden;
}
.statistics .screen-left .screen-left-content > div {
  padding-top: 0 !important;
  padding-bottom: 20px !important;
  height: 100%;
}
